
import { defineComponent, reactive, ref, onMounted } from 'vue';
import { useStore } from 'vuex'
import { useRouter, useRoute } from 'vue-router'
import axios from '@/axios/index';
import bcrypt from 'bcryptjs';
import { formContextKey, type FormInstance, type FormRules } from 'element-plus'
import { ElMessage } from 'element-plus'
export default defineComponent({
    setup() {
        const store = useStore();
        const router = useRouter();
        const route = useRoute();
        const employeeData = ref([])
        const dialogVisible = ref(false);
        const dialogTitle = ref('')
        const ruleFormRef = ref<FormInstance>()
        const roleOpt: any = ref([])
        const date = new Date();
        let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        let month = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
        let year = date.getFullYear();
        let currentDate = `${year}${month}${day}`;
        const form = reactive({
            name: '',
            acc: '',
            pass1: '',
            pass2: '',
            pass3: '',
            birth: '',
            phone: '',
            date: '',
            role: ''
        })
        const checkPass = (rule: any, value: any, callback: any) => {
            if (value === '') {
                callback(new Error('不得為空'))
            } else {
                axios.post('/checkPass', { pass: form.pass1, acc: form.acc }).then((res: any) => {

                    if (bcrypt.compareSync(form.pass1, res.Pass)) {
                        callback()
                    } else {
                        ElMessage({
                            message: '原始密碼錯誤',
                            type: 'error'
                        })
                        callback(new Error('原始密碼錯誤'))
                    }
                }).catch((e: any) => {
                    console.log(e)
                })

            }
        }
        const rules = reactive<FormRules>({
            pass1: [{ validator: checkPass, trigger: 'blur' }]
        })

        onMounted(async () => {
            await getEmployee();
            await getRole();
        })
        const getEmployee = () => {
            axios.post('/getEmployee', {}).then((res: any) => {
                console.log('employee', res.data)
                employeeData.value = res.data
            }).catch((e: any) => {
                console.log(e)
            })
        }
        const getRole = () => {
            axios.post('/getRole', {}).then((res: any) => {
                console.log('getRole', res.data)
                roleOpt.value = res.roleOpt
            }).catch((e: any) => {
                console.log(e)
            })
        }
        const handleAdd = () => {
            dialogTitle.value = '新增員工'
            dialogVisible.value = true;
            form.name = ''
            form.acc = ''
            // form.pass = ''
            // form.pass2 = ''
            form.birth = ''
            form.phone = ''
            form.date = currentDate
            form.role = 'L02'
        }
        const handleEdit = (index: any, row: any) => {
            dialogTitle.value = '編輯員工'
            dialogVisible.value = true;
            form.name = row.NAME
            form.acc = row.ACC
            // form.pass = row.PASS
            // form.pass2 = ''
            form.birth = row.BIRTH
            form.phone = row.PHONE
            form.date = row.DATE
            form.role = row.ROLE
        }
        const handleDelete = (index: any, row: any) => {
            dialogTitle.value = '刪除'
            axios.post('/delEmployee', {
                name: form.name,
                birth: form.birth,
                acc: form.acc,
                phone: form.phone,
                date: form.date,
                role: form.role,

            })
                .then(async (res: any) => {
                    console.log(res)
                    if (res.result == '1') {
                        await getEmployee();
                        dialogVisible.value = false;
                    }
                    showMess(res.result)

                }).catch((e: any) => {
                    console.log(e)
                })
        }
        const handleReset = (index: any, row: any) => {
            dialogTitle.value = '修改密碼'
            dialogVisible.value = true;
            form.acc = row.ACC
            form.name = row.NAME
            form.phone = row.PHONE
            form.pass1 = ''
            form.pass2 = ''
            form.pass3 = ''

        }
        const handleSumbit = (formEl: FormInstance | undefined) => {
            if (!formEl) return
            formEl.validate((valid) => {
                if (valid) {
                    if (dialogTitle.value == '修改密碼') {
                        if (form.pass2 === form.pass3) {
                            const salt = bcrypt.genSaltSync(10);
                            const hash = bcrypt.hashSync(form.pass2, salt)
                            axios.post('/passReset', {
                                name: form.name,
                                acc: form.acc,
                                phone: form.phone,

                                pass: hash
                            })
                                .then(async (res: any) => {
                                    console.log(res)
                                    if (res.result == '1') {
                                        await getEmployee();
                                        dialogVisible.value = false;
                                    }
                                    showMess(res.result)

                                }).catch((e: any) => {
                                    console.log(e)
                                })
                        }
                    } else if (dialogTitle.value == '新增員工') {
                        //預設密碼
                        const salt = bcrypt.genSaltSync(10);
                        const hash = bcrypt.hashSync('123', salt)
                        // console.log('密碼＝＝＝>', hash)
                        axios.post('/addEmployee', {
                            name: form.name,
                            acc: form.acc,
                            birth: form.birth,
                            phone: form.phone,
                            date: form.date,
                            role: form.role,
                            pass: hash
                        })
                            .then(async (res: any) => {
                                console.log(res)
                                if (res.result == '1') {
                                    await getEmployee();
                                    dialogVisible.value = false;
                                }
                                showMess(res.result)


                            }).catch((e: any) => {
                                console.log(e)
                            })
                    } else if (dialogTitle.value == '編輯員工') {
                        axios.post('/editEmployee', {
                            name: form.name,
                            birth: form.birth,
                            acc: form.acc,
                            phone: form.phone,
                            date: form.date,
                            role: form.role,

                        })
                            .then(async (res: any) => {
                                console.log(res)
                                if (res.result == '1') {
                                    await getEmployee();
                                    dialogVisible.value = false;
                                }
                                showMess(res.result)

                            }).catch((e: any) => {
                                console.log(e)
                            })
                    }
                } else {
                    console.log('error submit!')
                    return false
                }
            })
        }
        const showMess = (errNum: any) => {
            console.log('showMess', errNum)
            if (errNum == '0') {
                ElMessage({
                    message: dialogTitle.value.substring(0, 2) + '失敗',
                    type: 'error',
                })
            } else if (errNum == '1') {

                ElMessage({
                    message: dialogTitle.value.substring(0, 2) + '成功',
                    type: 'success',
                })
            } else if (errNum == '2') {
                ElMessage({
                    message: '帳號重複',
                    type: 'error',
                })
            }
        }
        return {
            handleAdd,
            handleEdit,
            handleDelete,
            handleReset,
            handleSumbit,
            showMess,
            employeeData,
            dialogVisible,
            dialogTitle,
            form,
            ruleFormRef,
            roleOpt,
            rules
        }
    }
});
